export default{
    props: {
        value: {},
        message:String
    },
    data(){
        return{
            showMask: false,
            PhotoList:[
            {
                img:'banner3.jpg'
            }
            ]
        }
    },
    methods:{
        closeMask(){
            this.showMask = false;
        },
        closeBtn(){
            this.closeMask();
        },
        dangerBtn(){
            this.closeMask();
        },
        confirmBtn(){
            this.closeMask();
        },
        closePop(){
            this.closeMask();
        }
    },
    mounted(){
        this.showMask = this.value;
    },
    watch:{
        value(newVal, oldVal){
            this.showMask = newVal;
        },
        showMask(val) {
            this.$emit('input', val);
        }
    },
}




