import dialogBar from './Dialog/dialog.vue'
export default{
	data(){
		return{
			sendVal:false,
			sels: [], //列表选中列
			Pic:[
			{
				img:'banner.png',
				name:'Photo1'
			},
			{
				img:'banner2.png',
				name:'Photo2'
			},
			{
				img:'banner3.jpg',
				name:'Photo3'
			},
			{
				img:'banner4.jpg',
				name:'Photo4'
			},
			{
				img:'banner.png',
				name:'Photo5'
			}
			],
			PhotoName:''
		}
	},
	methods:{
		openMask(index){
			this.sendVal=true;
			this.PhotoName=this.Pic[index].name
		},
		//全选单选多选
		selsChange: function(sels) {
			this.sels = sels;
		},
		//显示新增界面
		handleAdd: function() {
		  this.dialogStatus = "create";
	      //this.addFormVisible = true;
	      this.dialogFormVisible = true;
	      this.editForm = {
	      	id: "0",
	      	name: "",
	      	sex: -1,
	      	age: 0,
	      	phone:"",
	      	birth: "",
	      	remark:"",
	      	addr: ""
	      }
	  },
	  //新增
	  createData: function() {
	  	this.$refs.editForm.validate(valid => {
	  		if (valid) {
	  			this.$confirm("确认提交吗？", "提示", {})
	  			.then(() => {
              //this.editLoading = true;
              //NProgress.start();
              this.editForm.id = (parseInt(Math.random() * 100)).toString() // mock a id
              let para = Object.assign({}, this.editForm);
              console.log(para);
              
              para.birth =
              !para.birth || para.birth == ""
              ? ""
              : util.formatDate.format(new Date(para.birth), "yyyy-MM-dd");
              addUser(para).then(res => {
                //this.addLoading = false;
                //NProgress.done();
                this.$message({
                	message: "提交成功",
                	type: "success"
                });
                this.$refs["editForm"].resetFields();
                this.dialogFormVisible = false;
                //this.addFormVisible = false;
                this.getUsers();
            });
          })
	  			.catch(e => {
              // 打印一下错误
              console.log(e);
          });
	  		}
	  	});
	  },
	  delet(index){
	  	this.$confirm("确认删除该记录吗?", "提示", {
				type: "warning"
			})
			.then(() => {
				this.UserList.splice(index,1);
				this.$message({
					message: "删除成功",
					type: "success"
				});
			})
			.catch(() => {});
	  }
	},
	components:{
		'dialog-bar':dialogBar
	}
}


